
  <template>
  <div>
    <el-divider content-position="left">Pro</el-divider>
    <el-row style="height: 800px;">
      <my-pro :menus="menus"
              :mode="mode"
              :navbar-theme="navbarTheme"
              :sidebar-theme="sidebarTheme"
              :fixed="fixed"
              :collapsible="collapsible"
              :rainbow="rainbow"
              :color-weak="colorWeak"
              :tab="tab"
              :breadcrumb="createBreadcrumb">

        <template v-slot:actions>
          <UserAction :dropdown-items="items" username="Admin" extra="" :avatar="{theme: 'primary'}"></UserAction>
          <IconAction icon="el-icon-message-solid" :badge="12"></IconAction>
          <IconAction icon="icon-poweroff" svg></IconAction>
        </template>

        <my-wrapper title=""
                    sub-title="">

          <my-container>
            <el-form ref="form" label-width="100px" size="small" style="padding: 20px">
              <el-form-item label="">
                <el-radio-group v-model="mode">
                  <el-radio-button label="sidebar"></el-radio-button>
                  <el-radio-button label="navbar"></el-radio-button>
                  <el-radio-button label="both">+</el-radio-button>
                </el-radio-group>
              </el-form-item>

              <el-form-item label="">
                <el-radio-group v-model="sidebarTheme">
                  <el-radio-button label="light"></el-radio-button>
                  <el-radio-button label="dark"></el-radio-button>
                  <el-radio-button label="primary"></el-radio-button>
                  <el-radio-button label="gradual">+</el-radio-button>
                </el-radio-group>
              </el-form-item>

              <el-form-item label="">
                <el-radio-group v-model="navbarTheme">
                  <el-radio-button label="light"></el-radio-button>
                  <el-radio-button label="dark"></el-radio-button>
                  <el-radio-button label="primary"></el-radio-button>
                  <el-radio-button label="gradual">+</el-radio-button>
                </el-radio-group>
              </el-form-item>

              <el-form-item label="">
                <el-switch v-model="fixed"></el-switch>
              </el-form-item>

              <el-form-item label="">
                <el-switch v-model="collapsible"></el-switch>
              </el-form-item>

              <el-form-item label="Tab">
                <el-switch v-model="tabEnabled"></el-switch>
              </el-form-item>

              <el-form-item label="">
                <el-switch v-model="breadcrumbEnabled"></el-switch>
              </el-form-item>

              <el-form-item label="">
                <el-switch v-model="colorWeak"></el-switch>
              </el-form-item>
              <el-form-item label="">
                <el-switch v-model="rainbow"></el-switch>
              </el-form-item>
              <el-form-item label="">
                <el-switch v-model="scrollEnabled"></el-switch>
              </el-form-item>


            </el-form>
            <div v-if="scrollEnabled" style="height: 1000px"></div>
          </my-container>

        </my-wrapper>
      </my-pro>
    </el-row>
  </div>
</template>

<script>
  
     import __vue__ from 'vue';
     import {Divider as __Divider__,Form as __Form__,FormItem as __FormItem__,RadioButton as __RadioButton__,RadioGroup as __RadioGroup__,Row as __Row__,Switch as __Switch__} from 'element-ui';
     import {MyContainer as __MyContainer__,MyPro as __MyPro__,MyWrapper as __MyWrapper__} from '$ui';
     
     
     
     import {MyPro} from '$ui'
  import '$ui/icons/poweroff'

  const {IconAction, UserAction} = MyPro
  const mock = [
    {
      icon: 'el-icon-setting',
      text: '',
      index: '/one',
      badge: 10
    },
    {
      icon: 'el-icon-s-marketing',
      text: '',
      index: '/two',
      disabled: true
    },
    {
      icon: 'el-icon-s-data',
      text: '',
      index: '/three',
      badge: {
        value: 123,
        max: 9,
        type: 'primary'
      },
      children: [
        {
          group: true,
          title: '',
          index: 1,
          children: [
            {
              text: '',
              icon: 'el-icon-tickets',
              index: '/three/1',
              badge: {isDot: true}
            },
            {
              text: '',
              icon: 'el-icon-tickets',
              index: '/three/2'
            }
          ]
        },
        {
          group: true,
          title: '',
          index: 2,
          children: [
            {
              text: '',
              icon: 'el-icon-tickets',
              index: '/three/3'
            },
            {
              text: '',
              icon: 'el-icon-tickets',
              index: '/three/4'
            }
          ]
        }
      ]
    },
    {
      icon: 'el-icon-menu',
      text: '',
      index: '/four'
    },
    {
      icon: 'el-icon-share',
      text: '',
      index: '/five',
      children: [
        {
          text: '',
          icon: 'el-icon-tickets',
          index: '/five/1'
        },
        {
          text: '',
          icon: 'el-icon-tickets',
          index: '/five/2'
        }
      ]
    }
  ]
  export default {
    components: {
      MyPro,
      IconAction,
      UserAction
    },
    data() {
      return {
        menus: mock,
        mode: 'sidebar',
        sidebarTheme: 'light',
        navbarTheme: 'light',
        fixed: false,
        collapsible: true,
        colorWeak: false,
        rainbow: true,
        tabEnabled: false,
        breadcrumbEnabled: false,
        scrollEnabled: false,
        items: [
          {
            text: '',
            icon: 'el-icon-setting'
          },
          {
            text: '',
            divided: true
          }
        ]
      }
    },
    computed: {
      tab() {
        if (!this.tabEnabled) return null
        return {
          label: '',
          value: '/',
          icon: 'el-icon-setting'
        }
      }
    },
    methods: {
      createBreadcrumb() {
        if (!this.breadcrumbEnabled) return null
        return [
          {
            label: '',
            to: '/'
          },
          {label: ''},
          {label: ''}
        ]
      }
    }
  }
     __vue__.use(__Divider__);
__vue__.use(__Form__);
__vue__.use(__FormItem__);
__vue__.use(__RadioButton__);
__vue__.use(__RadioGroup__);
__vue__.use(__Row__);
__vue__.use(__Switch__)
     __vue__.use(__MyContainer__);
__vue__.use(__MyPro__);
__vue__.use(__MyWrapper__)
    
  </script>

