
  <template>
  <div>
    <el-divider content-position="left">Table</el-divider>
    <my-table :columns="columns" :loader="loader" :page="2" :page-size="5"></my-table>
  </div>
</template>

<script>
  
     import __vue__ from 'vue';
     import {Divider as __Divider__} from 'element-ui';
     import {MyTable as __MyTable__} from '$ui';
     
     
     
     import Mock from 'mockjs'

  export default {
    data() {
      return {
        columns: [
          {
            type: 'index',
            label: '#'
          },
          {
            label: '',
            prop: 'name'
          },
          {
            label: '',
            prop: 'id'
          },
          {
            label: '',
            prop: 'age'
          },
          {
            label: '',
            prop: 'address'
          },
          {
            label: '',
            prop: 'date'
          }
        ]
      }
    },
    methods: {
      mock(limit) {
        return Mock.mock({
          [`list|${limit}`]: [
            {
              id: '@id',
              name: '@cname',
              'age|10-100': 18,
              address: '@ctitle',
              date: '@date(yyyy-MM-dd)'
            }
          ]
        })
      },
      loader(page, pageSize) {
        return new Promise(resolve => {
          setTimeout(() => {
            resolve({
              total: 1000,
              list: this.mock(pageSize).list
            })
          }, 1000)
        })
      }
    }
  }
     __vue__.use(__Divider__)
     __vue__.use(__MyTable__)
    
  </script>

  